.app {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    overflow-x: hidden;
}

p {
    margin: 0;
    padding: 0;
}

.button {
    padding: 10px 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 4px;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
    cursor: pointer;
    background-color: white;
    color: black;
    border: 2px solid #555555;
    border-radius: 5px;
}

.button:hover {
    background-color: #555555;
    color: white;
}

ul.navbar {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    background-color: #333;
}

ul.navbar > li {
    height: 60px;
    background-color: #333;
    vertical-align: middle;
}

li.nav-left {
    float: left;
}

li.nav-right {
    float: right;
}

ul.navbar > li > a, ul.navbar > li > span {
    display: block;
    padding: 20px;
    text-decoration: none;
    color: white;
}

ul.navbar > li > a:hover {
    background-color: #111;
}

.screen {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    max-width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.screen h4 {
    width: 100%;
    margin: 10px;
    text-align: center;
}

.screen > div {
    padding-left: 10px;
    padding-right: 10px;
}

@media(min-width: 768px) {
    .screen > .half-screen {
        width: 50%;
    }

    .screen > .fifth-of-screen {
        width: 20%;
    }
}

.screen > div {
    -ms-flex-order: 2;
        order: 2;
}

@media(max-width: 768px) {
    .screen > div.pull-up-on-mobile {
        -ms-flex-order: 1;
            order: 1;
    }

    .button {
        padding: 3px 5px;
        margin: 16px 4px;
    }

    .half-screen {
        width: 100%;
    }
    .fifth-of-screen {
        width: 100%;
    }
}

.screen > div > p {
    padding: 0.5em;
    text-align: justify;
}

@media(min-width: 768px) {
    img {
        max-height: 300px;
        margin: 0px 30px;
    }
}

img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
}

ul.contacts {
    list-style-type: none;
    margin: 0;
    padding: 0;
    width: 100%;
    background-color: #f1f1f1;
}

ul.contacts > li > a {
    display: block;
    color: #000;
    padding: 8px 16px;
    text-decoration: none;
    text-align: center;
}

ul.contacts > li > a:hover {
    background-color: #555;
    color: white;
}

.iconed-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    width: 100%;
}

.iconed-list-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    height: 150px;
}

.iconed-list-item-icon {
    background-color: lightslategrey;
    font-size: 32px;
    min-width: 64px;
}

.iconed-list-item-icon > .fa, .iconed-list-item-icon > .fab {
    text-align: center;
    padding: 58px 16px;
}

.iconed-list-item-text {
    background-color: lightgray;
    text-align: justify;
    padding: 15px;
    width: 100%;
}

@media(min-width: 768px) {
    .iconed-list-item {
        width: 33%;
    }
}

@media(max-width: 768px) {
    .iconed-list-item {
        width: 100%;
    }
}


